.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.img {
  width: 500px; 
  height: auto; 
  margin-right: 20px;
}
.wrapper {
  text-align: center;
}

h1 {
  font-size: 24px;
  margin-bottom: 10px;
}

p {
  font-size: 18px;
  margin-bottom: 20px;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

input[type="file"] {
  display: none;
}

a {
  font-size: 16px;
  text-decoration: none;
  color: #007bff;
  margin-top: 10px;
}

a:hover {
  text-decoration: underline;
}